import React, { Component } from 'react';
import "./Skill.css"

export class Skill extends Component {
    render() {
        let percent = parseInt(this.props.percent.split('%')[0]);

        let barStyle = {
            width: `${100 - percent}%`,
            height: "100%",
        };

        let currentBarStyle = {
            left: `${percent - 1}%`,
            position: "relative",
            top: "-7.5%",
            height: "115%",
            width: `2%`,
        };

        return (
            <div className="skill">
                <p>{this.props.skill}</p>
                <div className="base-bar">
                    <div style={barStyle} className="percent-bar"></div>
                    <div style={currentBarStyle} className="current-percent-bar"></div>
                </div>
            </div>
        );
    }
}

export default Skill;